* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: white;
}

body {
  background-color: rgb(9, 78, 9);
  padding: 2rem;
}

nav {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  background-color: brown;
}

nav a {
  font-size: 1.5rem;
  text-decoration: none;
  font-weight: 600;
}

nav a:not(nav a:last-child) {
  margin-right: 2rem;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 1rem;
  margin-top: 1rem;
  background-color: darkgreen;
}

.yard {
  background-color: black;
}

.col {
  display: flex;
  flex-direction: column;
  width: 30%;
  padding: 1rem;
  background-color: darkgray;
}

.col h2 {
  margin-bottom: 1rem;
}

.collection {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 1rem;
  background-color: darkgreen;
}

.yard .collection {
  background-color: black;
}

.collection:not(.collection:last-child) {
  margin-bottom: 1rem;
}

.details {
  display: flex;
  flex-direction: column;
}

button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 2rem;
  padding: 1rem;
  cursor: pointer;
  background-color: chocolate;
  border: 1px solid white;
}

.search-icon {
  position: relative;
  left: 0.5rem;
}

.icon {
  font-size: 1rem;
}

.search {
  background-color: transparent;
  border: 2px solid white;
  margin-bottom: 1rem;
  padding: 0.5rem;
  padding-left: 2rem;
  font-size: 1rem;
  position: relative;
  left: -1rem;
}